<template>
	<div id="trash-detail">
		<!-- <h1>noteId:{{$route.query.noteId}}</h1> -->
		<h1>正在开发中...</h1>
	</div>
</template>
<script type="text/javascript">
	import Auth from '@/apis/auth'
	export default{
		data() {
			return{
				msg:'回收站笔记详情'
			}
		},
		created(){
			Auth.getInfo()
				.then(res=>{
					if(!res.isLogin){
						this.$router.push({path:'/login'})
					}
				})
	}}
</script>
<style type="text/css" scoped>
	#trash-detail{
		width:100%;
		height:100%;
		text-align: center;
	}
	h1{
		color:#ccc;
		font-size: 50px;
		margin-top: 100px;

	}
</style>


